<template>
  <nav-bar>
    <span slot="title" class="title">账号管理</span>
  </nav-bar>
  <div class="content-form">
    <h5><i class="fa fa-database"></i> 修改资料</h5>
    <form @submit="update" accept-charset="UTF-8">
      <div class="form-group row">
        <label class="col-sm-2 form-control-label" for="merchant_email">邮箱</label>
        <div class="col-sm-10">
          <input v-model="merchant.email" class="form-control" type="text" />
        </div>
      </div>
      <div class="form-group row">
        <label class="col-sm-2 form-control-label" for="merchant_name">用户名</label>
        <div class="col-sm-10">
          <input v-model="merchant.name" class="form-control" type="text" />
        </div>
      </div>
      <div class="form-group row">
        <div class="col-sm-offset-2 col-sm-10">
          <input type="submit" name="commit" value="保存" class="btn btn-primary" />
        </div>
      </div>
    </form>
    <br/>
    <h5><i class="fa fa-key"></i> 修改密码</h5>
    <form @submit="updatePassword" accept-charset="UTF-8">
      <div class="form-group row">
        <label class="col-sm-2 form-control-label">密码</label>
        <div class="col-sm-10">
          <input v-model="merchant.password" class="form-control" type="password" />
        </div>
      </div>
      <div class="form-group row">
        <label class="col-sm-2 form-control-label">确认密码</label>
        <div class="col-sm-10">
          <input v-model="merchant.password_confirmation" class="form-control" type="password" />
        </div>
      </div>
      <div class="form-group row">
        <label class="col-sm-2 form-control-label">当前密码</label>
        <div class="col-sm-10">
          <input v-model="merchant.current_password" class="form-control" type="password" />
        </div>
      </div>
      <div class="form-group row">
        <div class="col-sm-offset-2 col-sm-10">
          <input type="submit" name="commit" value="保存" class="btn btn-primary" />
        </div>
      </div>
    </form>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        merchant: {
          email: '',
          name: '',
          password: '',
          password_confirmation: '',
          current_password: ''
        }
      }
    },
    route: {
      data (transition) {
        this.$http.get('http://jiancan.me/api/m1/merchants/current.json', { access_token: localStorage.token }).then(function (response) {
          transition.next({
            merchant: response.data
          })
        }, function (response) {
          this.$dispatch('response-msg', response)
        })
      }
    },
    methods: {
      update (e) {
        e.preventDefault()

        var formData = new FormData()
        formData.append('access_token', localStorage.token)

        formData.append('merchant[name]', this.merchant.name)
        formData.append('merchant[email]', this.merchant.email)
        this.$http.put('http://jiancan.me/api/m1/merchants/current.json', formData).then(function (response) {
          this.$set('merchant', response.data.merchant)
        }, function (response) {
          this.$dispatch('response-msg', response)
        })
      },
      updatePassword (e) {
        e.preventDefault()

        var formData = new FormData()
        formData.append('access_token', localStorage.token)

        formData.append('merchant[password]', this.merchant.name)
        formData.append('merchant[password_confirmation]', this.merchant.password_confirmation)
        formData.append('merchant[current_password]', this.merchant.current_password)

        this.$http.put('http://jiancan.me/api/m1/merchants/current/password.json', formData).then(function (response) {
          this.$set('merchant', response.data.merchant)
        }, function (response) {
          this.$dispatch('response-msg', response)
        })
      }
    },
    components: {
      'nav-bar': require('../components/nav-bar.vue')
    }
  }
</script>



